.page {
  font-family: "Poppins", sans-serif;
  font-style: normal;
  font-weight: $font-bold;
  font-size: 24px;
  line-height: $line-hight;
  color: $white-color;
  scroll-behavior: smooth;

  &__body {
    margin: 0;
    min-width: 320px;
  }

  &__menu {
    position: fixed;
    top: 0;
    left: 0;
    right: 0;
    opacity: 0;
    pointer-events: none;
    transition: all #{$effectDuration}s;
    transform: translateX(-100%);

    &:target {
      opacity: 1;
      transform: translateX(0);
      pointer-events: all;
    }
  }
  &:has(.page__menu:target) {
    overflow: hidden;
  }
}
